"use client";

import { useState, useEffect, useRef, useCallback } from 'react';

export function useSpeech() {
  const [isListening, setIsListening] = useState(false);
  const [transcript, setTranscript] = useState('');
  const recognitionRef = useRef<any>(null);
  const [isSupported, setIsSupported] = useState(false);
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const [isSpeaking, setIsSpeaking] = useState(false);
  const utteranceRef = useRef<SpeechSynthesisUtterance | null>(null);
  const [voices, setVoices] = useState<SpeechSynthesisVoice[]>([]);

  const populateVoiceList = useCallback(() => {
    if (typeof window !== 'undefined' && window.speechSynthesis) {
      try {
        const newVoices = window.speechSynthesis.getVoices();
        console.log("üì¢ Available voices:", newVoices.length);
        if (newVoices.length > 0) {
          setVoices(newVoices);
        }
      } catch (error) {
        console.error("Error loading speech synthesis voices:", error);
      }
    }
  }, []);

  useEffect(() => {
    // --- Speech Recognition Setup ---
    if (typeof window !== 'undefined') {
      const SpeechRecognition = (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;
      if (SpeechRecognition) {
        setIsSupported(true);
        const recognition = new SpeechRecognition();
        recognition.continuous = false;
        recognition.lang = 'en-US';
        recognition.interimResults = false;

        recognition.onresult = (event: any) => {
          const currentTranscript = event.results[0][0].transcript;
          setTranscript(currentTranscript);
          setIsListening(false);
        };

        recognition.onerror = (event: any) => {
          console.error('Speech recognition error', event.error);
          setIsListening(false);
        };
        
        recognition.onend = () => {
          setIsListening(false);
        };

        recognitionRef.current = recognition;
      } else {
        setIsSupported(false);
      }
    }

    // --- Speech Synthesis Setup ---
    if (typeof window !== 'undefined' && window.speechSynthesis) {
        populateVoiceList();
        if (window.speechSynthesis.onvoiceschanged !== undefined) {
            window.speechSynthesis.onvoiceschanged = populateVoiceList;
        }
        
        // Initialize speech synthesis with a silent utterance to prepare it
        try {
          const initUtterance = new SpeechSynthesisUtterance('');
          initUtterance.volume = 0;
          window.speechSynthesis.speak(initUtterance);
          
          // Chrome fix: Force resume audio context after initialization
          setTimeout(() => {
            if (window.speechSynthesis.paused) {
              window.speechSynthesis.resume();
            }
          }, 100);
        } catch (error) {
          console.warn("Could not initialize speech synthesis:", error);
        }
    }

    // --- Audio Player and Synthesis Cleanup ---
    const cleanup = () => {
        try {
          if (audioRef.current) {
              audioRef.current.pause();
              audioRef.current.src = "";
          }
          if (typeof window !== 'undefined' && window.speechSynthesis) {
              window.speechSynthesis.cancel();
          }
        } catch (error) {
          console.error("Error during cleanup:", error);
        }
    };

    return cleanup;
  }, [populateVoiceList]);
  
  const stopSpeaking = useCallback(() => {
    try {
      if (audioRef.current) {
          audioRef.current.pause();
          audioRef.current.currentTime = 0;
          audioRef.current.src = "";
      }
      if (typeof window !== 'undefined' && window.speechSynthesis) {
          if (window.speechSynthesis.speaking || window.speechSynthesis.pending) {
              window.speechSynthesis.cancel();
          }
      }
    } catch (error) {
      console.error("Error stopping speech:", error);
    } finally {
      setIsSpeaking(false);
    }
  }, []);


  const speak = useCallback((text: string, audioDataUri?: string, forceBrowserVoice = false) => {
    // Validate input
    if (!text || text.trim() === '') {
      console.warn("Cannot speak empty text");
      return;
    }
    
    console.log("üîä Attempting to speak:", text);
    
    // Chrome-specific: Resume audio context if suspended
    if (typeof window !== 'undefined' && window.speechSynthesis) {
      try {
        // Force resume on Chrome - fixes autoplay policy issues
        if (window.speechSynthesis.paused) {
          console.log("üîÑ Resuming speech synthesis (Chrome fix)");
          window.speechSynthesis.resume();
        }
      } catch (error) {
        console.warn("Could not resume speech synthesis:", error);
      }
    }
    
    // Stop any audio playback first
    try {
      if (audioRef.current) {
          audioRef.current.pause();
          audioRef.current.currentTime = 0;
          audioRef.current.src = "";
      }
    } catch (error) {
      console.error("Error stopping audio:", error);
    }

    const speakBrowser = () => {
        if (typeof window !== 'undefined' && window.speechSynthesis && text) {
            // FIRST: Cancel any ongoing speech
            if (window.speechSynthesis.speaking || window.speechSynthesis.pending) {
                console.log("üõë Canceling previous speech...");
                window.speechSynthesis.cancel();
            }
            
            // THEN: Wait and create new utterance
            setTimeout(() => {
                try {
                    // Create fresh utterance
                    const utterance = new SpeechSynthesisUtterance(text);
                    
                    let selectedVoice = voices.find(voice => voice.name === 'Google US English' && voice.lang === 'en-US');
                    if (!selectedVoice) selectedVoice = voices.find(voice => voice.lang === 'en-US' && !voice.localService);
                    if (!selectedVoice) selectedVoice = voices.find(voice => voice.lang === 'en-US');

                    if (!selectedVoice) {
                        if (voices.length === 0) {
                             console.warn("‚ö†Ô∏è Speech synthesis voices not loaded yet. Retrying...");
                             setTimeout(() => speak(text, audioDataUri, forceBrowserVoice), 250);
                             return;
                        }
                         console.warn("‚ö†Ô∏è Could not find 'en-US' voice, using default.");
                    }
                    
                    console.log("üé§ Using voice:", selectedVoice?.name || "default");
                    
                    utterance.voice = selectedVoice || null;
                    utterance.lang = 'en-US';
                    utterance.rate = 0.9;
                    utterance.volume = 1.0;

                    utterance.onstart = () => {
                        console.log("‚úÖ Speech STARTED:", text);
                        setIsSpeaking(true);
                    };
                    
                    utterance.onend = () => {
                        console.log("‚úÖ Speech ENDED:", text);
                        setIsSpeaking(false);
                    };
                    
                    utterance.onerror = (e: SpeechSynthesisErrorEvent) => {
                        // Ignore "canceled" errors - they're expected
                        if (e.error === 'canceled' || e.error === 'interrupted') {
                            console.log("‚ÑπÔ∏è Speech canceled (expected)");
                            setIsSpeaking(false);
                            return;
                        }
                        
                        // Log real errors
                        console.error("‚ùå SpeechSynthesis REAL Error:", {
                            error: e.error,
                            type: e.type,
                            text: text
                        });
                        setIsSpeaking(false);
                        
                        // Retry logic for recoverable errors
                        if (e.error === 'network' || e.error === 'synthesis-failed') {
                            console.log("üîÑ Retrying speech synthesis...");
                            setTimeout(() => {
                                try {
                                    if (window.speechSynthesis) {
                                        window.speechSynthesis.speak(utterance);
                                    }
                                } catch (retryError) {
                                    console.error("Retry failed:", retryError);
                                }
                            }, 500);
                        }
                    };
                    
                    // Store reference
                    utteranceRef.current = utterance;
                    
                    // Chrome fix: Resume before speaking
                    if (window.speechSynthesis.paused) {
                        window.speechSynthesis.resume();
                    }
                    
                    // Speak it!
                    console.log("üó£Ô∏è SPEAKING NOW:", text);
                    window.speechSynthesis.speak(utterance);
                    
                    // Debug check
                    setTimeout(() => {
                        console.log("üìä Status check:", {
                            speaking: window.speechSynthesis.speaking,
                            pending: window.speechSynthesis.pending,
                            paused: window.speechSynthesis.paused
                        });
                        
                        if (!window.speechSynthesis.speaking && !window.speechSynthesis.pending) {
                            console.error("‚ö†Ô∏è Speech was called but not started!");
                            console.error("Possible issues: System volume muted, browser audio blocked, or need user interaction");
                        }
                    }, 100);
                    
                } catch (error) {
                    console.error("Speech synthesis error:", error);
                    setIsSpeaking(false);
                }
            }, 150); // Wait 150ms after cancel
        } else {
            console.warn("Speech synthesis not available or no text provided");
            setIsSpeaking(false);
        }
    };

    // Execute
    try {
      if (forceBrowserVoice || (!audioDataUri && text)) {
          speakBrowser();
      } else if (audioDataUri) {
          if (!audioRef.current) {
              audioRef.current = new Audio();
          }
          const audio = audioRef.current;
          
          const onPlaying = () => setIsSpeaking(true);
          const onEnded = () => {
              setIsSpeaking(false);
              audio.removeEventListener('playing', onPlaying);
              audio.removeEventListener('ended', onEnded);
              audio.removeEventListener('pause', onEnded);
              audio.removeEventListener('error', onError);
          };
          
          const onError = () => {
              console.error("Audio playback error, falling back to browser TTS.");
              onEnded();
              speakBrowser();
          };

          audio.addEventListener('playing', onPlaying);
          audio.addEventListener('ended', onEnded);
          audio.addEventListener('pause', onEnded);
          audio.addEventListener('error', onError);
          
          try {
              audio.src = audioDataUri;
              audio.play().catch(error => {
                  console.error("Failed to play AI audio, falling back to browser TTS.", error);
                  onEnded();
                  speakBrowser();
              });
          } catch (error) {
              console.error("Error setting audio source, falling back to browser TTS.", error);
              onEnded();
              speakBrowser();
          }
      } else {
          setIsSpeaking(false);
      }
    } catch (error) {
      console.error("Error in speak function:", error);
      setIsSpeaking(false);
    }
  }, [voices]);

  const startListening = useCallback(() => {
    if (recognitionRef.current && !isListening) {
      setTranscript('');
      recognitionRef.current.start();
      setIsListening(true);
    }
  }, [isListening]);

  const stopListening = useCallback(() => {
    if (recognitionRef.current && isListening) {
      recognitionRef.current.stop();
      setIsListening(false);
    }
  }, [isListening]);

  return { speak, startListening, stopListening, isListening, transcript, isSupported, setTranscript, isSpeaking, stopSpeaking };
}
